<template>
  <div id="app">
    <Headers id="headers"></Headers>
    <main id="main">
      <router-view/>
    </main>
    <Footers id="footers"></Footers>
  </div>
</template>

<script>
  import Headers from './components/headers'
  import Footers from './components/footers'
export default {
  name: 'App',
  components:{
    Headers,Footers
  }
}
</script>

<style>
  @import "./assets/Markdown.css";
  *{
    padding: 0;
    margin: 0;
  }
  html,body,#app{
    height: 100%;
  }
  #app{
    display: grid;
    grid-template-columns: 12% auto 12%;
    grid-template-rows: auto 1fr auto;
    grid-template-areas: 'header header header'
                          '.     main     .  '
                          'footer footer footer';

  }
  #headers{
    grid-area: header;
    padding-left: 12%;
    padding-right: 12%;
    background: #aec950;
  }
  #main{
    grid-area: main;
  }
  #footers{
    grid-area: footer;
    padding-left: 12%;
    padding-right: 12%;
  }
  @media (max-width: 768px) {
    #app{
      grid-template-columns: 10px auto 10px;
    }
    #headers,#footers{
      padding-left: 10px;
      padding-right: 10px;
      min-width: 450px;
    }
  }
  .isloading{
    font-size: 16px;
    text-align: center;
    color: #aeca4f;
    padding: 10px 0 10px 0;
  }
  .el-icon-loading{
    font-size: 30px;
    vertical-align: middle;
  }
</style>



