import auth from "../../api/auth";

const state ={
  user: null,
  isLogin : false
}

const getters ={
  isLogin:state => state.isLogin,
  user:state => state.user
}

const mutations ={
  setUser(state,payload){  //用户状态
    state.user = payload.user
  },
  setLogin(state,payload){ //isLogin的状态
    state.isLogin = payload.isLogin
  }
}

const actions = {
  login({commit},{ username,password }){ //用户登录后状态的改变逻辑
    return auth.login({ username,password })
      //如果不返回，调用login时没有任何变化，如果返回就是返回一个promise可以then
      .then(res =>{
        commit('setUser',{ user:res.data })
        commit('setLogin',{ isLogin: true })
      })
  },
  register({commit},{ username,password }){//用户注册后状态的改变逻辑
    return auth.register({ username,password })
      .then(res =>{
        commit('setUser',{ user:res.data })
        commit('setLogin',{ isLogin: true })
      })
  },
  loginout({commit}){
    return auth.loginout()
      .then(res=>{
        commit('setUser',{ user: null })
        commit('setLogin',{ isLogin: false })
      })
  },
  async checkLogin({commit,state}){
    if (state.isLogin) return true
    let res =await auth.checkLogin()
    commit('setLogin',{ isLogin:res.isLogin })
    if(!res.isLogin) return false
    commit('setUser',{ user: res.data })
    return true
  }
  // checkLogin({ commit,state }){
  //   if(!state.isLogin){
  //     console.log('此时用户没登录，state.isLogin值为false')
  //     return auth.checkLogin()
  //       .then(res=>{
  //         if(res.isLogin){
  //           commit('setLogin',{ isLogin: res.isLogin });
  //           commit('setUser',{ user: res.data });
  //         }else{
  //           commit('setLogin',{ isLogin: res.isLogin });
  //           commit('setUser',{ user: null });
  //         }
  //       })
  //   }else{
  //     console.log('这时用户已登录，state.isLogin值为false')
  //   }
  // }

}
export default {
  state,
  getters,
  mutations,
  actions
}



