<template>
    <div>
      <header class="header-one" v-if="isLogin">
        <h1><router-link to="/">LET'S SHARE</router-link></h1>
        <div class="icon_img">
          <router-link to="/create">
            <span><i class="edit el-icon-edit"></i></span>
          </router-link>
        <div class="menu">
          <img class="img" :src="user.avatar">
<!--          http://blog-server.hunger-valley.com/avatar/70.jpg-->
          <ul class="user_ul">
            <router-link to="/myblog"><li>我的</li></router-link>
            <li @click="onloginout">注销</li>
          </ul>
        </div>
        </div>
      </header>
      <header class="header-tow" v-else>
        <h1><router-link to="/">LET'S SHARE</router-link></h1>
        <p>精品博客汇聚</p>
        <router-link to="/login"><el-button>立即登录</el-button></router-link>
        <router-link to="/register"><el-button>注册账号</el-button></router-link>
      </header>
    </div>
</template>

<script>
  import { mapGetters,mapActions } from 'vuex'
  import {Message} from "element-ui";
    export default {
      name: "headers",
      data(){
        return{
        }
      },
      computed:{
        ...mapGetters(['isLogin','user'])
      },
      created() {
        this.checkLogin()
      },
      methods:{
        ...mapActions(['checkLogin','loginout']),
        onloginout(){
          this.loginout()
          .then(()=>{
            Message.success('成功注销')
            this.$router.push('/');
          })
        }
      }
    }
</script>

<style scoped>
.header-one{
  text-align: left;
  color: #ffffff;
}
.header-one h1 {
  font-size: 40px;
  margin:10px 0;
  display: inline-block;
}
.header-one h1 a{
  color: #ffffff;
  text-decoration: none;
}
.el-icon-edit{
  font-size: 45px;
  color:#ffffff;
  padding: 10px 10px;
  cursor:pointer;
  vertical-align: middle;
}
.icon_img{
  float: right;
  display: inline-block;
  padding: 1px 5px;
}
.menu{
  display: inline-block;
  vertical-align: middle;
  position:relative
}
.img{
  vertical-align: middle;
  margin: 5px 10px;
  width: 60px;
  height: 60px;
  border:1px solid #ffffff;
  border-radius: 50%;
}
.header-tow{
  text-align: center;
  color: #ffffff;
  padding: 20px 0;
}
.header-tow h1{
  margin: 15px 0;
  font-size: 40px;
}
.header-tow h1 a{
  color: #ffffff;
  text-decoration: none;
}
.header-tow p{
  padding: 10px 0;
  font-size: 20px;
}
.el-button{
  margin:15px 10px;
  background-color: #ff9900;
  border-color: #ff9900;
  border-radius: 6px;
  color: #ffffff;
  font-size: 15px;
}
.el-button:active{
  background-color:#ffb23e;
  border-color: #ffb23e;
}

.user_ul{
  display: none;
  list-style: none;
  position: absolute;
  bottom:-63px;
  right: 0px;
  background-color:rgba(175,200,80,0.9);
  /*background-color:rgb(255, 153, 0,0.8);*/
}
.user_ul li{
  width: 80px;
  height: 20px;
  padding: 5px 0px 5px 0px;
  border-bottom: 1px solid;
  text-align: center;
  color: #ffffff;
  cursor: pointer;
}
.user_ul a{
  text-decoration: none;
}
.menu:hover .user_ul{
  display: block;
}
</style>



